@import "dds__version";
@import "dds__icon-variables";


@font-face {
  font-family: 'dds-icons';
  font-style: normal;
  font-weight: normal;
  src: url('//uicore.dellcdn.com/#{$version}/icons/dds-icons.woff2') format('woff2'), /* Pretty Modern Browsers */
    url('//uicore.dellcdn.com/#{$version}/icons/dds-icons.woff') format('woff'), /* Super Modern Browsers */
    url('//uicore.dellcdn.com/#{$version}/icons/dds-icons.ttf') format('truetype'), /* Safari, Android, iOS */
    url('//uicore.dellcdn.com/#{$version}/icons/dds-icons.eot'), /* IE9 Compat Modes */
    url('//uicore.dellcdn.com/#{$version}/icons/dds-icons.eot?#iefix') format('embedded-opentype'); /* IE6-IE8 */
  
}

//SVG Icon Usage
.dds__flag-icons {
  display: inline-block;
  fill: currentColor;
  height: 2rem;
  stroke: currentColor;
  stroke-width: 0;
  width: 2rem;
}

.dds__svg-icons {
  display: inline-block;
  fill: currentColor;
  height: 3rem;
  stroke: currentColor;
  stroke-width: 0;
  width: 3rem;
}

// Icon css
.dds__icons {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'dds-icons';

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // speak: none;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  line-height: 1;
  text-transform: none;
}

//=======================================================
//------------ code for grid display only ---------------
//=======================================================
.dds__icon-grid-container {
  align-items: center;
  font-size: 3rem;
  height: 100%;
  outline: 0.0625rem solid $dell-ash;
  padding: 0.625rem;

  .dds__icon-grid-item {
    color: $dell-blue;
    float: left;
    font-size: 3rem;
    margin: 0.625rem;
  }

  .dds__icon-grid-text {
    font-size: 1rem;
    margin-bottom: 0.0325rem;
    padding: 0.3125rem;
  }

  .dds__icon-grid-text-muted {
    color: $dell-gray;
    font-size: 0.675rem;
    padding: 0.3125rem;
  }
}

//=======================================================
//=======================================================

.dds__icon-svg-grid-container {
  align-items: center;
  display: flex;
  font-size: 3rem;
  height: 100%;
  justify-content: flex-start;
  outline: 0.0625rem solid $dell-ash;
  padding: 0.625rem;

  .dds__icon-svg-grid-item {
    align-items: center;
    color: $dell-blue;
    display: inherit;
    font-size: 3rem;
    margin: 0.625rem;
  }

  .dds__icon-grid-text {
    font-size: 1rem;
    margin-bottom: 0.0325rem;
    padding: 0.3125rem;
  }

  .dds__icon-grid-text-muted {
    color: $dell-gray;
    font-size: 0.675rem;
    padding: 0.3125rem;
  }

  ol {
    padding-inline-start: 10px;
  }
}


